#drinks-and-foods header {
  background-color: var(--i-secondary);
  width: 100%;
  z-index: 20;
}

#drinks-and-foods .header-main {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

#drinks-and-foods .header-page-title {
  font-size: 30px;
  padding: 5px 0;
}

#drinks-and-foods .header-main button {
  background-color: transparent;
  border: none;
}

#drinks-and-foods .search-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
}

#drinks-and-foods .search {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  margin-top: 15px;
  width: 340px;
}

#drinks-and-foods .radio-input {
  margin-left: 5px;
}

#drinks-and-foods .btt-search {
  border: 1px solid gainsboro;
  border-radius: 10px;
  box-shadow: 7px 5px 3px rgba(124, 124, 124, 0.57);
  color: #252525;
  font-size: 20px;
  font-weight: 700;
  height: 40px;
  margin-bottom: 15px;
  width: 100px;
}

#drinks-and-foods .btt-search:active {
  box-shadow: 3px 2px 1px rgba(124, 124, 124, 0.57);
  transform: translateY(4px);
}
